:root {
  --list-back: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  --favorites-back: "black";
  --details-back: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  --details-box-back: linear-gradient(to left top, #051937, #004d7a, #008793, #00bf72, #a8eb12);;
  --header-back: #000000;
  --button-back: #cecd26;
  --border-color: #c2dde6;
  --list-box-back: #01A185;
}

body {
  background-image: -webkit-linear-gradient(left bottom, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  background-image: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,h2,h3,h4,h5,h6 {
  font-family: Menlo;
  font-weight: normal;
  margin: 0;
  font-size: 1.5em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

.header {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 10px;
  background: #000000;
  background: var(--header-back);
  color: white;
  padding: 10px;

}
.sticky{
  position: fixed;
  top: 0;
  width: 100%
}
.header-titles h1 {
  font-size: 2em;
}
.header p {
  margin: 0;
}
button {
  margin: 0.3em;
  background: #cecd26;
  background: var(--button-back);
  border: 0;
  color: white;
  padding: 10px;
  border-radius:5px;
  font-size: 1em;
}

.container {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 47% auto;
  grid-gap: 0;

}

.photos {
  display: grid;
  border-radius: 20px;
  grid-template-columns: repeat(auto-fit, 250px);
  grid-gap: 5px;
  background-color: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  background-color: var(--list-back);
  overflow-y: auto;
  height:120vh;

}

.photoBox {
  display: grid;
  grid-template-columns: 110px 1fr;
  grid-gap: 2px;
  background-color: #01A185;
  background-color: var(--list-box-back);
  margin: 0.75rem;
  padding: 0.5rem;
  place-self: center center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;


}
.photoBox button {
  padding: 5px;
  font-size: 0.75em;
  margin: 0;
  border-radius: 60px;
}
.photoBox h3 {
  font-size: 0.85em;
  font-weight: bold;
}
.photoBox p {
  font-size: 0.75em;
  margin: 0.25em 0 0.25em 0;
}
.photoBox button {
  margin: 0.3em;
  min-width: 2.5em;

}

img.photoThumb {
  width: 100px;
  height: 100px;
}


.details {
  background-color: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  background-color: var(--details-back);
  padding: 0.5em;
  width: 95%
}
.detailsPhotoBox {
  box-sizing: border-box;
  margin: 0.75em;
  padding: 1em;
  background-color: linear-gradient(to left top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  background-color: var(--details-box-back)
}

figure {
  margin: 0;
  padding: 0;

}
legend {
  font-weight: bold;
  color: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
  color: var(--list-back);
  font-size: 1.2em;
  font-family: Menlo;
  text-align: center;
}
.photoForm img {
  width: 100%
}

.favorites {
  display: grid;
  grid-template-columns: repeat(15, 1fr);
  grid-auto-flow: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 5px;
  background: "black";
  background: var(--favorites-back);
  color: #000000;
  color: var(--header-back);
  padding: 5px;
  font-size: 0.8em;
  font-weight: bold;
  text-transform: uppercase;
  overflow-x: auto;
  height: 15vh;

}
.wrapper {
  position: relative;
  display: inline-block;
}


.pin {
 
  position:absolute;
  /*url of the marker*/
  background:url(http://maps.gstatic.com/mapfiles/markers2/marker.png) no-repeat;
  /*center the marker*/
  top:50%;left:50%;
  z-index:1;
  /*fix offset when needed*/
  margin-left:-10px;
  margin-top:-34px;
  /*size of the image*/
  height:34px;
  width:20px;
  cursor:pointer;
}
.photoFave .photoThumb .after {
  display: block;
  background: rgba(0, 0, 0, .6);
}

.photoBox {
    position: relative;
    width: 100%;
}

.image {
    display: block;
    width: 100%;
    height: auto;
}

.overlay2{
    position: absolute;
    background-color: #a8eb12;
    top: 8px;
    right: 16px;
    bottom: 0;
    left: 0;
    overflow: hidden;
    width: 10%;
    height: 10%;
    -webkit-transform: scale(0);
    transform: scale(0);
    -webkit-transition: .3s ease;
    transition: .3s ease;
}

.photoBox:hover .overlay2 {
    -webkit-transform: scale(1);
    transform: scale(1);
}

.text2 {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    right:100%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-align: center;
}
